.ScreenBlocker_screenBlocker{
	background-color: rgba(192,192,192,0.3);
	position: fixed;
	padding: 0;
	margin: 0;
	top:0;
	left:0;
	width:100%;
	height: 100%;
	z-index: 10;
}

.ScreenBlocker_parentBlocker{
	width:100%;
	height: 100%;
	z-index: 100;
	left: 0;
	top: 0;
	position: absolute;
	background-color: rgba(192,192,192,0.3);
	padding: 0;
	margin: 0;
}




